<style lang="less" scoped>
    li {
        margin-top: 10px;
    }
    p {
        height: 30px;
        line-height: 30px;
        background: #007dd4;
    }
    p:empty {
        background: #c7254e;
    }
    p:empty::before {
        content: "placeholder"
    }
    p:not(:empty):focus::before {
        content: ""
    }
    .contenteditable {
        padding-left: 10px;
        outline: none;
        cursor: text;
    }
</style>

<template>
    <main>
        <nav>xxxx</nav>
        <h1>:empty ()</h1>
        <ul>
            <li>
                <p>
                    <span></span>
                </p>
            </li>
            <li>
                <p></p>
            </li>
            <li>
                <p contenteditable="true" class="contenteditable"></p>
            </li>
        </ul>
    </main>
</template>

<script>
    module.exports = {};
</script>



